.main-container {
  display: flex;
  font-family: Arial, sans-serif;
  height: 100vh;
  margin: 0;
}


.sidebar {
  width: 200px;
  background-color: #b393c7;
  background-image: url("sidebar-styler2.jpg");
  background-size: 100% 100%;
  border: 2px #b393c7;
  padding: 20px;
}

.sidebar .BB_Logo{
  max-width: 100%;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  padding: 15px 0;
  cursor: pointer;
  transition: color 0.5s;
}

.sidebar li:hover{ 
  color: blueviolet;
}

.sidebar li.active {
  font-weight: bold;
}


.content {
  flex: 1;
  padding: 20px;
  background-color: #f9f9f9;
  max-height: 100vh;
  /* box-sizing: border-box;
  overflow-y: auto;  */
}

a {
  all: unset;
}
