  
  .journal-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    /* border: 2px solid gray; */
    padding: 10px;
  }
  
  
  .journal-item {
    background-color: #fff;
    padding: 15px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    text-align: center;
    cursor: pointer;
    transition: background-color 0.5s;
    height: 250px;
  }
  
  .journal-item:hover{
    background-color: aliceblue;
  }
  
  .journal-image {
    /* width: 100%; */
    height: 100px;
    object-fit: cover;
    margin-bottom: 10px;
    background-color: gold;
    transition: background-color 0.5s;
    border: solid gold;
    cursor: pointer;
  }
  
  .journal-image:hover{
    background-color: blue;
  }
  
  .journal-item h3 {
    margin: 10px 0;
  }
  